.container{
    background-color: #222222;
    height: 100%;
    scroll-behavior: smooth;
    position: relative;
    cursor: none;
}
.header{
    color: white;
    font-family: 'New Rocker', sans-serif;
    font-weight: 400;
    font-size: 80px;
    position: absolute;
    z-index: 1;
}
.headerMob{
    color: white;
    font-family: 'New Rocker', sans-serif;
    font-weight: 400;
    font-size: 60px;
    z-index: 1;
    margin-bottom: 10px;
    display: none;
}
.text{
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 44px;
    color: white;
    position: absolute;
    bottom: 5%;
}
.legend{
    position: absolute;
    right: 2%;
    bottom: 3%;
    z-index: 2;
    width: 12%;
}
.mask{
    position: absolute;
    background-color: #222222a1;
    width:100%;
    height: 100%;
}
.mapImage{
    height: 100%;
}
.imagep{
  height: 100%;
  background: url('../../static/map.png');
  object-fit: cover;
  object-position: center;
  background-size: cover;
  background-position: center;
  position: relative;
  display: flex;
  justify-content: center;
}
.imageMask{
    z-index: 1;
    margin-top: -10px;
}
.mapTablet{
    display: none;
}
.svgImage{
    margin-top: -3px;
    height: 101%;
}
.mapMob{
    display: none;
}
@media (max-width:1200px) {
    .container{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: unset;
        flex-direction: column;
        cursor: unset;
    }
    .headerMob{
        display: unset;
        margin-top: 5px;
        font-size: 9vw;
    }
    .mapMob{
        display: unset;
        width: 100%;
        margin-bottom: 20px;
    }
    .imagep{
        display: none;
    }
}